import { memo } from "react";
import { Hidden, Switch, Box, styled, useTheme } from "@mui/material";
import { themeShadows } from "app/components/MatxTheme/themeColors";
import useSettings from "app/hooks/useSettings";
import { convertHexToRGB } from "app/utils/utils";
import { sidenavCompactWidth, sideNavWidth } from "app/utils/constant";
import Brand from "../../Brand";
import Sidenav from "../../Sidenav";
import { Themecolors, fonts } from "api/Colors";

// Enhanced UI color configuration
const colors = {
  light: {
    background: "#FFFFFF", // Light mode background
    text: "#333333", // Light mode text
  },
  dark: {
    background: "#121212", // Dark mode background
    text: "#FFFFFF", // Dark mode text
  },
};

const SidebarNavRoot = styled(Box)(({ theme, width, bg, image, mode }) => ({
  position: "fixed",
  top: 0,
  left: 0,
  height: "100vh",
  width: width,
  boxShadow: themeShadows[8],
  backgroundImage: `url(${image})`,
  backgroundRepeat: "no-repeat",
  backgroundPosition: "top",
  backgroundSize: "cover",
  zIndex: 111,
  overflow: "hidden",
  color: Themecolors.S_text1,
  fontFamily: fonts.poppins,
  background: "#6b73ac",
  transition: "all 250ms ease-in-out",
  // "&:hover": {
  //   width: mode === "full" ? sideNavWidth : width,
  //   "& .sidenavHoverShow": { display: "block" },
  //   "& .compactNavItem": {
  //     width: "100%",
  //     maxWidth: "100%",
  //     "& .nav-bullet": { display: "block" },
  //     "& .nav-bullet-text": { display: "none" },
  //   },
  // },
}));

const NavListBox = styled(Box)({
  height: "100%",
  display: "flex",
  flexDirection: "column",
  backgroundColor: "#6b73ac",
});

const Layout1Sidenav = () => {
  const theme = useTheme();
  const { settings, updateSettings } = useSettings();
  const leftSidebar = settings.layout1Settings.leftSidebar;
  const { mode, bgImgURL } = leftSidebar;

  const getSidenavWidth = () => {
    switch (mode) {
      case "compact":
        return sidenavCompactWidth;

      default:
        return sideNavWidth;
    }
  };

  const updateSidebarMode = (sidebarSettings) => {
    updateSettings({
      layout1Settings: { leftSidebar: { ...sidebarSettings } },
    });
  };

  const handleSidenavToggle = () => {
    updateSidebarMode({ mode: mode === "compact" ? "full" : "compact" });
  };

  return (
    <SidebarNavRoot
      image={bgImgURL}
      bg={theme.palette.primary.main}
      width={getSidenavWidth()}
    >
      <NavListBox>
        <Brand>
          <Hidden smDown>
            <Switch
              onChange={handleSidenavToggle}
              checked={leftSidebar.mode !== "full"}
              color="secondary"
              size="small"
            />
          </Hidden>
        </Brand>
        <Sidenav />
      </NavListBox>
    </SidebarNavRoot>
  );
};

export default memo(Layout1Sidenav);
