import {
  Box,
  styled,
  Icon,
  Button,
  useMediaQuery,
  useTheme,
} from "@mui/material";
import { MatxLogo } from "app/components";
import useSettings from "app/hooks/useSettings";
import { Span } from "./Typography";
import appStore from "../mobxStore/AppStore";
import { observer } from "mobx-react";
import { useEffect, useState } from "react";
import GWIcon from "../../api/Icon/GW-Icon.png";
import LocationOnIcon from "@mui/icons-material/LocationOn";

const Image = styled("img")({
  height: "50px",
  width: "45px",
});

const BrandRoot = styled(Box)(() => ({
  display: "flex",
  flexDirection: "column",
  alignItems: "left",
  justifyContent: "space-between",
  paddingTop: "20px",
  paddingBottom: "5px",
}));

const StyledSpan = styled(Span)(({ mode }) => ({
  fontSize: 24,
  display: mode === "compact" ? "none" : "block",
  color: "white",
  marginTop: "6px",
}));

const Brand = observer(({ children }) => {
  const theme = useTheme();

  const { settings, updateSettings } = useSettings();
  const leftSidebar = settings.layout1Settings.leftSidebar;
  const { mode } = leftSidebar;
  const [userEntity, setUserEntity] = useState("");

  const updateSidebarMode = (sidebarSettings) => {
    updateSettings({
      layout1Settings: { leftSidebar: { ...sidebarSettings } },
    });
  };

  useEffect(() => {
    const name =
      appStore.userEntities.find(
        (entity) => entity.entityId === appStore.selectedEntity
      )?.entityName ?? " ";
    setUserEntity(name);
  }, [appStore.selectedEntity, appStore.userEntities]);

  return (
    <BrandRoot>
      <Box
        display="flex"
        alignItems="center"
        sx={{ position: "relative", bottom: "18px" }}
      >
        <Image src={GWIcon} alt="Logo" sx={{ mb: 1.5 }} />
        <StyledSpan mode={mode} className="sidenavHoverShow">
          <strong
            style={{
              borderRadius: "20px",
              padding: "2px 2px",
            }}
          >
            Grad Walk
          </strong>
        </StyledSpan>
      </Box>
      <Box
        display="flex"
        alignItems="center"
        sx={{
          position: "relative",
          ml: "1em",
          bottom: "2em",
          display: mode === "compact" ? "none" : "flex",
        }}
      >
        {userEntity && (
          <>
            <LocationOnIcon
              sx={{
                color: "white",
                fontSize: "15px",
                marginRight: "8px",
              }}
            />
            <StyledSpan
              sx={{
                fontSize: "1.1em",
                position: "relative",
                bottom: "0.15em",
              }}
            >
              {`${userEntity}`}
            </StyledSpan>
          </>
        )}
      </Box>
    </BrandRoot>
  );
});

export default Brand;
