import React from "react";
import TextField from "@mui/material/TextField";
import { Themecolors } from "api/Colors";

interface TextInputFieldProps {
  label: string;
  id: string;
  value: string;
  onChange: (event: React.ChangeEvent<HTMLInputElement>) => void;
  type?: string;
  error?: boolean;
  helperText?: string;
  required?: boolean;
}

const TextInputField: React.FC<TextInputFieldProps> = ({
  label,
  id,
  value,
  onChange,
  type = "text",
  error = false,
  helperText = "",
  required = false,
}) => {
  return (
    <TextField
      id={id}
      value={value}
      onChange={onChange}
      type={type}
      fullWidth
      variant="outlined"
      error={error}
      helperText={helperText}
      required={required}
      InputLabelProps={{
        sx: {
          color: Themecolors.InputText_Color1,
          "&.Mui-focused": {
            color: Themecolors.InputText_Color1,
          },
        },
      }}
      sx={{
        "& .MuiOutlinedInput-root": {
          "& .MuiInputBase-input": {
            paddingY: "10px",
          },
          borderRadius: "4px",
          "& fieldset": {
            borderColor: "#bdbdbd",
          },
          "&:hover fieldset": {
            borderColor: Themecolors.InputBorder2,
          },
          "&.Mui-focused fieldset": {
            borderColor: Themecolors.InputBorder2,
          },
        },
      }}
      InputProps={{
        sx: {
          color: Themecolors.InputText_Color1,
          "& .MuiInputBase-input": {
            color: Themecolors.InputText_Color1,
          },
        },
      }}
    />
  );
};

export default TextInputField;
